import {
    createTaskAction,
    deleteAllCompletedTasksAction,
    deleteTaskAction,
    toggleTaskCompletionAction,
    updateTaskTextAction,
    createSectionAction,
    deleteSectionAction,
    updateSectionTextAction,
    toggleSectionAction,
    clearAllSectionsAndTasksAction,
    getToggledStatusAction,
    pushCompletedTaskAction,
    setSectionToStackAction,
    setTasksToStackAction,
    updateTaskPriorityAction,
    setSectionPriorityAction, shareTaskAction, removeTaskAction
} from "./actions"
import store from "./store.js"
class TaskDataController {
    static updateTaskText(id, identifier, newText) {
            const action = updateTaskTextAction(id, identifier ,newText)
            store.dispatch(action)
    }

    static toggleTaskCompletion(id, identifier, isToggled) {
        const action = toggleTaskCompletionAction(id, identifier, isToggled)
        store.dispatch(action)
    }

    static createTask(sectionIdentifier) {
        const action = createTaskAction(sectionIdentifier)
        store.dispatch(action)
    }

    static deleteTask(id) {
        const action = deleteTaskAction(id)
        store.dispatch(action)
    }


    static deleteAllCompleted() {
        const action = deleteAllCompletedTasksAction()
        store.dispatch(action)
    }

    static todo() {
        return store.getState()
        .tasks.filter(task => task.isCompleted === false)
    }

    static completed() {
        return store.getState()
        .tasks.filter(task => task.isCompleted === true)
    }

    // new function to return general tasks, not finished yet

    static getTasks(identifier) {
        return store.getState().sections.filter(section => section.identifier === identifier)
    }

    // new function to get sections

    static getSections() {
        return store.getState().sections
    }


    static createSection() {
        const action = createSectionAction()
        store.dispatch(action)
    }

    static deleteSection(sectionIdentifier) {
        const action = deleteSectionAction(sectionIdentifier)
        store.dispatch(action)
    }

    static updateSectionText(sectionIdentifier,text) {
        const action = updateSectionTextAction(sectionIdentifier,text)
        store.dispatch(action)
    }

    static toggleSection(sectionIdentifier) {
        const action = toggleSectionAction(sectionIdentifier)
        store.dispatch(action)
    }

    static clearAllSectionsAndTasks() {
        const action = clearAllSectionsAndTasksAction()
        store.dispatch(action)
    }

    static getToggledStatus(sectionIdentifier){
        const action = getToggledStatusAction(sectionIdentifier)
        store.dispatch(action)
    }

    static pushCompletedTask(Task) {

        const action = pushCompletedTaskAction(Task)
        store.dispatch(action)
    }

    static setSectionToStack(stackList){
        const action = setSectionToStackAction(stackList)
        store.dispatch(action)
    }

    static setTasksToStack(taskList){
        const action = setTasksToStackAction(taskList)
        store.dispatch(action)
    }

    static updateTaskPriority(id, sectionIdentifier, value){
        const action = updateTaskPriorityAction(id, sectionIdentifier, value)
        store.dispatch(action)
    }

    static setSectionPriority(value){
        const action = setSectionPriorityAction(value)
        store.dispatch(action)
    }

    static shareTask(inputEmail){
        const action = shareTaskAction(inputEmail)
        store.dispatch(action)
    }

    static removeTask(){
        const action = removeTaskAction()
        store.dispatch(action)
    }


};


export default TaskDataController